.dmc-profile {
  padding: 8px;
}

.dmc-profile-layout-grid__inner {
  grid-gap: 8px;
}

@media (max-width: 839px) {
  .dmc-profile {
    padding: 0px;
  }

  .dmc-profile-layout-grid__inner {
    grid-gap: 0px;
  }

  .dmc-profile-avatar {
    display: none;
  }
}

.dmc-profile-detail,
.dmc-profile-avatar {
  background: #fff;
  background: var(--mdc-theme-background, #fff);
}

.dmc-profile-avatar {
  .mdc-card__media img {
    width: 100%;
    height: 100%;
    max-width: 512px;
    max-height: 512px;
  }
}

.dmc-profile-avatar__media {
  position:relative;
  display: block;
  padding: 0;

  >div {
    position: absolute;
    top:      0;
    left:     0;
    bottom:   0;
    right:    0;
    background-image: -webkit-gradient(linear, 0 0, 100% 100%, color-stop(0.25, rgba(0, 0, 0, 0.1)), color-stop(0.25, transparent), color-stop(0.5, transparent), color-stop(0.5, rgba(0, 0, 0, 0.1)), color-stop(0.75, rgba(0, 0, 0, 0.1)), color-stop(0.75, transparent), to(transparent));
    background-size: 50px 50px;
    animation: animate-stripes 2s linear infinite;
  }
}

.dmc-profile-avatar__media:before {
  content:     "";
  display:     block;
  padding-top: 100%;
}

.dmc-profile-avatar__shadow {
  position: absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background: radial-gradient(transparent 70%, #3f51b5 70%);
  background: radial-gradient(transparent 70%, var(--mdc-theme-primary, #3f51b5) 70%);
}


.dmc-profile-avatar__change {
  overflow: hidden;
  text-overflow: ellipsis;

  input[type=file] {
    &::-webkit-file-upload-button {
      display: none;
    }

    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    margin: 0;
    padding: 0;
    font-size: 20px;
    cursor: pointer;
    opacity: 0;
    filter: alpha(opacity=0);
  }
}

.dmc-profile-avatar__change--disabled:not(:disabled) {
  background-image: -webkit-gradient(linear, 0 0, 100% 100%, color-stop(0.25, rgba(0, 0, 0, 0.1)), color-stop(0.25, transparent), color-stop(0.5, transparent), color-stop(0.5, rgba(0, 0, 0, 0.1)), color-stop(0.75, rgba(0, 0, 0, 0.1)), color-stop(0.75, transparent), to(transparent));
  background-size: 50px 50px;
  animation: animate-stripes 2s linear infinite;
  cursor: default;
  pointer-events: none;
  background-color: rgba(0, 0, 0, 0.12);
  color: rgba(0, 0, 0, 0.38);
  color: var(--mdc-theme-text-disabled-on-light, rgba(0, 0, 0, 0.38));
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.2), 0px 0px 0px 0px rgba(0, 0, 0, 0.14), 0px 0px 0px 0px rgba(0, 0, 0, 0.12);
}

